.App {
  font-family: sans-serif;
  text-align: center;
}

.todo-item:hover {
  background-color: lavenderblush;
}

.todo-item {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 20px;
  width: 90%;
  border-bottom: 1px solid violet;
  text-indent: 10px;
  font-family: consolas;
  font-weight: 900;
}

.todo-list {
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 4px solid rgb(202, 233, 233);
  box-shadow: 5px;
  background-color: rgb(234, 211, 211);
}
